import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withRouter } from 'react-router-dom'
import fileDownload from 'js-file-download'
import withTranslations from 'Shared/hoc/withTranslations'
import Loader from 'Shared/components/Loader/Loader'
import './EmployerDownloadFileUtility.scss'

const REDIRECT_TIMEOUT = 5000

class EmployerDownloadFileUtility extends Component {
  async componentDidMount () {
    await this.downloadFiles()
  }

  downloadFiles = async () => {
    const { match: { params: { filename } } } = this.props
    try {
      const res = await this.props.downloadRoute(filename)

      fileDownload(res.data, filename)
      setTimeout(() => {
        window.location.href = `${window.location.origin}/employer/job-offers/active`
      }, REDIRECT_TIMEOUT)
    } catch (err) {
      console.warn('Could not download file:', err)
      // TODO: replace this url path with employers dashboard, when it is available
      window.location.href = `${window.location.origin}/employer/job-offers/active`
    }
  }

  render () {
    return (
      <div className='employer-payment__container parsing--hidden'>
        <div className='employer-payment-redirect-utility'>
          <Loader
            className='employer-plan-payment__loader'
            active
            description={this.props.t('files.downloading', {timeout: REDIRECT_TIMEOUT / 1000})}
          />
        </div>
      </div>
    )
  }
}

EmployerDownloadFileUtility.propTypes = {
  downloadRoute: PropTypes.func.isRequired
}

export default withRouter(withTranslations(EmployerDownloadFileUtility))



